import React from 'react'
import { InfoContainer, InfoWrapper, InfoRow, Column1, Column2, ImgWrap, TextWrapper, TopLine, Subtitle, SubtitleItalics, Img } from './infoelements';
import { useEffect } from 'react';
import $ from 'jquery';

const InfoSection = ({ lightBg, imgStart, topLine, lightText, headline, description, darkText, buttonLabel, imgs, alt, id, primary, dark, dark2 }) => {

  const [abouttext, setabouttext] = React.useState([]);
  useEffect(() => {
    function myXhr(path) {
      return $.ajax({
        type: 'GET',
        url: 'http://serenity.ist.rit.edu/~plgics/proxy.php',
        data: path,
        cache: false,
        async: true,
        dataType: "json",
      });
    }

    myXhr({ path: '/about/' }).done(function (json) {
      setabouttext(json);
    });
  }, []);



  return (
    <>
      <InfoContainer lightBg={lightBg} id={id}>
        <InfoWrapper>
          <InfoRow imgStart={imgStart}>
            <Column1>
              <TextWrapper>
                <TopLine>{abouttext.title}</TopLine>
                <Subtitle darkText={darkText}>{abouttext.description}</Subtitle>
                <SubtitleItalics darkText={darkText}>"{abouttext.quote}"</SubtitleItalics>
                <SubtitleItalics darkText={darkText}>           -{abouttext.quoteAuthor}"</SubtitleItalics>
              </TextWrapper>
            </Column1>
            <Column2>
              <ImgWrap>
                <Img src={imgs.default} alt={alt} />
              </ImgWrap>
            </Column2>
          </InfoRow>
        </InfoWrapper>
      </InfoContainer>

    </>
  )
};

export default InfoSection;
