import React from 'react';
import './App.css';
import {BrowserRouter as Router, Switch,Route} from 'react-router-dom';
import Home from './pages';
import signInPage from './pages/signin';
import Staff from './pages/staff';
import Faculty from './pages/faculty';
import research from './pages/research';
import ResearchArea from './components/Research';


function App() {
  return (
    <Router>
      <Switch>
        <Route path= "/" component = {Home} exact />
        <Route path= "/signin" component = {signInPage} exact />
        <Route path= "/staff" component = {Staff} exact />
        <Route path= "/faculty" component = {Faculty} exact />
        <Route path= "/research" component = {research} exact />
        <Route path= "/researcharea" component = {ResearchArea} exact />
      </Switch>
    </Router>
  );
}

export default App;
