import React, { useEffect } from 'react'
import $ from 'jquery';
import { FooterContainer, FooterWrap, FooterLinkContainer, FooterLinkWrapper, FooterLinkItems, FooterLinkTitle } from './FooterElements'
import { ExternalLink } from 'react-external-link';



const Footer = () => {
    const [Data, setgraduate] = React.useState([]);
    const [link, setquicklink] = React.useState([]);
    const [copy, setcopyright] = React.useState([]);
    const [news, setnews] = React.useState([]);
    useEffect(() => {
        function myXhr(path) {
            return $.ajax({
                type: 'GET',
                url: 'http://serenity.ist.rit.edu/~plgics/proxy.php',
                data: path,
                cache: false,
                async: true,
                dataType: "json",
            });
        }

        myXhr({ path: '/footer/' }).done(function (json) {
            setgraduate(json.social);
            setquicklink(json.quickLinks);
            setcopyright(json.copyright);
            setnews(json.news);

        });
    }, []);
    return (
        <FooterContainer>
            <FooterWrap>
                <FooterLinkContainer>
                    <FooterLinkWrapper>

                        <FooterLinkItems>
                            <FooterLinkTitle>{Data.title}</FooterLinkTitle>
                            <FooterLinkTitle>{Data.tweet}</FooterLinkTitle>
                            <ExternalLink href={Data.twitter}>Twitter</ExternalLink>
                            <ExternalLink href={Data.facebook}> Facebook </ExternalLink>
                        </FooterLinkItems>

                        <FooterLinkItems>

                            <FooterLinkTitle>Some Quick Links</FooterLinkTitle>
                            {link.map(function (fac, index) {
                                return (
                                    <li>
                                        <ExternalLink href={fac.href}>{fac.title}</ExternalLink>
                                    </li>
                                )
                            })}

                        </FooterLinkItems>
                    </FooterLinkWrapper>
                    <FooterLinkWrapper>
                        <FooterLinkItems>
                            <FooterLinkTitle>{copy.title}</FooterLinkTitle>
                            {copy.html}
                            <ExternalLink href={news}> News </ExternalLink>
                        </FooterLinkItems>

                    </FooterLinkWrapper>
                </FooterLinkContainer>
            </FooterWrap>
        </FooterContainer>

    )
}

export default Footer