import React, { useRef, useEffect, useCallback } from 'react'
import { useSpring, animated } from 'react-spring'
import styled from 'styled-components';
import { MdClose } from 'react-icons/md'

const Background = styled.div`
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.8);
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
`;
const ModalWrapper = styled.div`
    overflow: auto;
    width: 800px;
    height: 500px;
    box-shadow: 0 5px 16px rgba(0,0,0,0.2);
    background: #fff;
    color: #000;
    display: grid;
    grid-template-columns: 1fr 1fr;
    position: relative;
    z-index: 10;
    border-radius: 10px;

    @media screen and (max-width: 800px){
       
        width: 400px;
        height: 250px;
    }
    @media screen and (max-width: 480px){
        width: 200px;
        height: 125px;
        grid-template-columns: 1fr;
    }
`;
const ModalImg = styled.img`
    width: 100%;
    height: 100%;
    border-radius: 10px 0 0 10px;
    background: #000;
`;

const ModalContent = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    line-height: 1.8;
    color: #141414;
`;

const textModal = styled.p`
    margin-bottom: 1rem;
`;
const Heading = styled.h1`
    font-weight: bold;
    color: #e67300;
`;

const CloseModalButton = styled(MdClose)`
    cursor: pointer;
    position: absolute;
    top: 20px;
    right: 20px;
    width: 32px;
    height: 32px;
    padding: 0;
    z-index: 10;
`;


export const Modal = ({ showModal, setShowModal, varPass }) => {
    console.log(varPass);

    const modalRef = useRef();

    const animation = useSpring({
        config: {
            duration: 250
        },
        opacity: showModal ? 1 : 0,
        transform: showModal ? `translateY(0%)` : `translateY(-100%)`
    });
    const closeModal = e => {
        if (modalRef.current === e.target) {
            setShowModal(false);
        }
    };

    const keyPress = useCallback(
        e => {
            if (e.key === 'Escape' && showModal) {
                setShowModal(false);
                console.log('I pressed');
            }
        },
        [setShowModal, showModal]
    );

    useEffect(
        () => {
            document.addEventListener('keydown', keyPress);
            return () => document.removeEventListener('keydown', keyPress);
        },
        [keyPress]
    );

    return (
        <>
            {showModal ? (
                <Background onClick={closeModal} ref={modalRef}>
                    <animated.div style={animation}>
                        <ModalWrapper showModal={showModal}>
                            <ModalImg src={varPass.imagePath} alt='modal' />
                            <ModalContent>
                                <Heading>{varPass.name}</Heading>
                                <textModal>Title : {varPass.title}</textModal>
                                <textModal>Office : {varPass.office}</textModal>
                                <textModal>E-mail : {varPass.email}</textModal>
                                <textModal>Departments : {varPass.interestArea}</textModal>
                                <textModal>Phone : {varPass.phone}</textModal>
                                <textModal>Research: {varPass.email}</textModal>
                            </ModalContent>
                            <CloseModalButton aria-label='Close modal' onClick={() => setShowModal(prev => !prev)} />
                        </ModalWrapper>
                    </animated.div>
                </Background>
            ) : null}
        </>
    );
}