import React, { useState } from 'react'
import Video from '../../videos/video.mp4'
import { Button } from '../ButtonElements'
import { MainContainer, MainBG, VideoBG, MainContent, MainH1, MainH2, MainBtnWrapper, ArrowForward, ArrowRight } from './mainElements'
const MainSection = () => {

    const [hover, setHover] = useState(false)

    const onHover = () => {
        setHover(!hover)
    }

    return (

        <MainContainer>

            <MainBG>
                <VideoBG autoPlay loop muted src={Video} type='video/mp4' />
            </MainBG>

            <MainContent>

                <MainH1>Rohester Institute of Technology</MainH1>
                <MainH2>Information Technology and Analytics</MainH2>
                <MainBtnWrapper>
                    <Button to='signup' onMouseEnter={onHover} onMouseLeave={onHover}>
                        Get Started {hover ? <ArrowForward /> : <ArrowRight />}
                    </Button>
                </MainBtnWrapper>

            </MainContent>

        </MainContainer>

    )
}

export default MainSection;
